import { Component, OnInit } from '@angular/core';
import { environment as env } from 'src/environments/environment';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { Funciones } from 'src/app/compartido/funciones';
import { AddProductComponent } from 'src/app/components/add-product/add-product.component';
import { DataService } from 'src/app/services/data-service.service';
import { ProductService } from 'src/app/services/product.service';

@Component({
  selector: 'app-productos',
  templateUrl: './productos.component.html',
  styleUrls: ['./productos.component.scss'],
})
export class ProductosComponent implements OnInit {

  constructor(
    private _mod: ModalController,
    public _data: DataService,
    public router: Router,
    private _fun: Funciones,
    private _product: ProductService,) { }

  list_main = [];
  pageOfItems: Array<any>;
  list = [];
  load = false;
  filterData: any = { textFilter: '' }

  ngOnInit() {
    this.getList();
  }

  async getList() {
    try {
      const res_list: any = await this._product.getAll();
      console.log('res_list',res_list);
      

   /*    if (this._fun.isVarInvalid(res_list.valid)) {
        this.load = false;
        return;
      } */

      this.list_main = res_list/* .res */;
      this.list = this.list_main;
      console.log('res_list00001', res_list);

    } catch (error) {
      await this._fun.alertError(error);
    }

  }



  producto(data) {
    this._data.dataProd.next(data);
    this.router.navigate(['/producto/' + data._id]);
  }


  async form(data?) {
    const modal = await this._mod.create({
      component: AddProductComponent,
      backdropDismiss: false,
      componentProps: {
        data
      }
    });

    modal.onDidDismiss().then(async (res: any) => {
      console.log(res.data);
      if (this._fun.isVarInvalid(res.data)) return;
      if (!this._fun.isVarInvalid(res.data.confirm)) this.getList();
    });
    return await modal.present();
  }

  async updateStatus(data){
    try {
      //  let dataSave = this.productForm.value;
      data.status = 3;

      const res: any = await this._product.save(data, data._id);
      console.log('res', res);
      if (this._fun.isVarInvalid(res.valid)) {
        return;
      }
      await this._fun.alertSucc(env.MSG.SUC_UPDATE);
    } catch (error) {
      await this._fun.alertError(error);
    }
  }

  async delete(data) {

    let alert = await this._fun.alertDelete();
    if (this._fun.isVarInvalid(alert)) return;

    try {
      const res: any = await this._product.delete(data._id);
      console.log('res', res);
      this.getList();
      await this._fun.alertSucc(env.MSG.SUC_DELETE);
    } catch (error) {
      await this._fun.alertError(error);
    }
  }

}
